import Vue from 'vue'
import App from './App.vue'

new Vue({
  el: '#app',
  render: h => h(App)
})

window.onscroll = function () {
  fixTableHead()
};

function fixTableHead() {
  // the element which will be added and fixed
  let theader = document.querySelector('#network-names');
  // the real header
  let theaderReal = document.querySelector('#network-names-real');
  // getting the top position
  var rect = theaderReal.getBoundingClientRect();
  // console.log(rect.top);
  if (rect.top <= 0) {
    // Making the width of table cells equal to the real one
    let ths = document.querySelectorAll('#network-names th');
    let thsReal = document.querySelectorAll("#network-names-real th");

    for (let i = 0; i < ths.length; i++) {
      ths[i].style.width = String(thsReal[i].clientWidth - 20) + 'px';
    }
    // changing the display
    theader.style.position = "fixed";
    theader.style.top = '0px';
    theader.style.display = 'block';
  } else {
    theader.style.position = "relative";
    theader.style.display = 'none';
  }
}



