  methods: {
    // dump() {
    //   console.log(JSON.stringify(this.filters));
    // },
    applyFilters(appliedFilters) {
      this.filteredNetworksIndices = [];
      if(appliedFilters.length>0){
        this.filtersApplied = true;
      }else{
        this.filtersApplied = false;
      }
      for (let i = 0; i < this.networks.length; i++) {
        // console.log("inside n loop");
        let passed = true;
        for (let j=0; j<appliedFilters.length; j++) {
          if (
            this.filters[ appliedFilters[j][0] ][ appliedFilters[j][1] ] === true &&
            this.networks[i][ appliedFilters[j][0] ][ appliedFilters[j][1] ] === false
          ) {
            passed = false;
            // console.log(i,filterType[filter],'failed');
            break;
          }
        }
        if (passed) {
          this.filteredNetworksIndices.push(i);
        }
      }
      // console.log(this.filteredNetworksIndices);
    },
    showAllNetworks() {
      if (! this.filtersApplied) {
        this.filteredNetworksIndices = [];
        for (var i = 0; i < this.networks.length; i++) {
          this.filteredNetworksIndices.push(i);
        }
      }
    }
