<template>
    <div id="filters-container">
        <div id="all-filters" v-bind:style="allFiltersStyle">
            <ul v-for="(filterObj,filterType,i) in filters" v-bind:key="i">
                <span class="filter-type">{{filterType}}</span>
                <hr>
                <li v-for="(value,key,index) in filterObj" v-bind:key="index">
                    <label>
                        <input  v-model="filterObj[key]" type="checkbox"  class='checkmark'>
                        <span>{{key}}</span>
                    </label>
                </li>
            </ul>
        </div>
        <!-- #filters end -->
        <div id="filters-bottom" v-bind:style="filtersBottomStyle">
            <div v-if="allFiltersStyle.height=='0px'"  @click="showAllFilters" id="all-filters-btn">
                <div>
                    &raquo;
                </div>
                Filters
            </div>
            <div v-if="allFiltersStyle.height!='0px'" @click="updateFilters" id="apply-filters-btn">
                Apply Filters
                <div>
                    &raquo;
                </div>
            </div>
            <div id="applied-filters-left">
                Applied Filters
            </div>
            <div v-if="appliedFilters.length<=0" id="applied-filters" style="margin-top:6px;">
                No filter has been applied
            </div>
            <div v-else id="applied-filters">
                <div v-for="(filterArr,index) in appliedFilters" :key="index">

                    {{filterArr[1]}}
                    <span @click="removeFilter(filterArr[0],filterArr[1])" class="icon sunset-cross" style="font-size:10px;"></span>
                </div>
            </div>
        </div>
    </div>

</template>
<script>
export default {
  name: "filters-container",
  props: ["filters"],
  data() {
    return {
      appliedFilters: [],
      allFiltersStyle: {
        height: "180px",
        opacity: "1",
        paddingTop: "50px"
      },
      filtersBottomStyle: {
        paddingTop: "80px"
      }
    };
  },
  methods: {
    updateFilters() {
      this.allFiltersStyle = {
        height: "0px",
        opacity: "0",
        paddingTop: "70px"
      };
      this.filtersBottomStyle = {
        paddingTop: "30px"
      };
      this.appliedFilters = [];
      for (let filterTypeKey in this.filters) {
        //   console.log(filterTypeKey);
        let filterType = this.filters[filterTypeKey];
        for (let filter in filterType) {
          // console.log(filterType[filter]);
          if (filterType[filter] === true) {
            // console.log(filter)
            this.appliedFilters.push([filterTypeKey, filter]);
          }
        }
      }
      // console.log("filterUpdated",this.appliedFilters);
      this.$emit("filterUpdated", this.appliedFilters);
    },
    showAllFilters() {
      this.allFiltersStyle = {
        height: "180px",
        opacity: "1",
        paddingTop: "50px"
      };
      this.filtersBottomStyle = {
        paddingTop: "80px"
      };
    },
    removeFilter(filterType, filterName) {
      (this.filters[filterType][filterName] = false), this.updateFilters();
    }
  },
  mounted() {
    this.updateFilters();
  }
};
</script>
<style lang="scss">
$background: #e74c3c;
$backgroundG : linear-gradient(83deg, #e74c3c 0%, #ce4536 16%, #b53d31 33%, #9d362c 50%, #852f26 66%, #6e2821 83%, #58211c 100%);
div#filters-container {
  margin-top: 1px;
  margin-bottom: 60px;
  background: $background;
  background: $backgroundG;
  color: white;

  div#all-filters {
    box-sizing: border-box;
    display: flex;
    justify-content: space-evenly;
    padding-bottom: 10px;
    height: 200px;
    overflow: hidden;
    // padding-top: 80px;
    transition: all 1.5s ease-out;
  }
  ul {
    hr{
        height: 0px;
        margin-top: 2px;
        border: 1px dashed rgb(243, 167, 167);
        opacity: 50%;
    }
    li {
        &:first-of-type{
            margin-top: 6px;
        }
      label {
        &:hover {
          cursor: pointer;
        }
        input.checkmark {
            &:hover {
                cursor: pointer;
            }
        }
      }
    }
  }
}
div#filters-bottom {
  position: relative;
  display: flex;
  justify-content: center;
  border-top: 1px solid rgb(243, 167, 167);
  width: 90%;
  margin: auto;
  padding-bottom: 30px;
  > div#applied-filters-left {
    display: inline-block;
    width: 100px;

    padding: 6px;
    padding-right: 0;
    align-self: center;
  }
  > div#applied-filters {
    margin: 0px 20px;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    border-left: 2px solid white;
    padding-left: 20px;
    div {
      margin: 10px 10px;

      border: 2px solid white;
      padding: 6px;
      border-radius: 25px;
      span {
        &:hover {
          cursor: pointer;
          font-weight: bold;
        }
      }
    }
  }
  $apply-filter-btn-size: 45px;
  div#apply-filters-btn {
    position: absolute;
    top: 0;
    height: $apply-filter-btn-size;
    width: $apply-filter-btn-size * 2;
    padding: 6px;
    text-align: center;
    border: 2px solid rgb(243, 167, 167);
    border-bottom-left-radius: ($apply-filter-btn-size) * 2;
    border-bottom-right-radius: ($apply-filter-btn-size) * 2;
    cursor: pointer;
    // background: orange;
    transition: padding-top 2s ease-out;
    div {
      font-size: 36px;
      padding-right: 30px;
      padding-bottom: 6px;
      animation: apply-filter-btn-animation 2s ease-in-out infinite forwards;
    }
  }
  $all-filter-btn-size: 45px;
  div#all-filters-btn {
    position: absolute;
    top: -($all-filter-btn-size + 10);
    height: $apply-filter-btn-size;
    width: $apply-filter-btn-size * 2;
    padding: 6px;
    padding-top: 0;
    text-align: center;
    border: 2px solid rgb(243, 167, 167);
    border-top-left-radius: ($apply-filter-btn-size) * 2;
    border-top-right-radius: ($apply-filter-btn-size) * 2;
    cursor: pointer;
    // background: orange;
    div {
      font-size: 36px;
      width: 36px;
      height: 26px;
      margin-left: 10px;
      animation: all-filters-btn-animation 2s ease-in-out infinite forwards;
    }
  }
}

@keyframes apply-filter-btn-animation {
  0% {
    transform: rotate(90deg);
  }
  50% {
    transform: translate(0.5px, 2px) rotate(90deg);
  }
  100% {
    transform: rotate(90deg);
  }
}
$degree: -90deg;
@keyframes all-filters-btn-animation {
  0% {
    transform: rotate($degree);
  }
  50% {
    transform: translate(0.5px, 2px) rotate($degree);
  }
  100% {
    transform: rotate($degree);
  }
}
</style>


